<template>
  <el-container class="container">
    <el-header class="header">
      <el-row>
        <el-col :span="4">
          <div class="grid-content bg-purple">
            <img src alt="无法显示图片" />
          </div>
        </el-col>
        <el-col :span="18" class="middle">
          <div class="grid-content bg-purple-light">
            <h3>电商后台管理系统</h3>
          </div>
        </el-col>
        <el-col :span="2">
          <div class="grid-content bg-purple">
            <a href="#" @click.prevent="handleSignout()" class="loginout">退出</a>
          </div>
        </el-col>
      </el-row>
    </el-header>
    <el-container>
      <el-aside width="200px" class="aside">
        <!--侧边栏导航-->
        <el-menu :router="true" :unique-opened="true">
          <el-submenu index="1">
            <template slot="title">
              <i class="el-icon-star-on"></i>
              <span>标签管理</span>
            </template>
            <el-menu-item index="tag">
              <i class="el-icon-menu"></i>
              <span>标签列表</span>
            </el-menu-item>
          </el-submenu>
		  <el-submenu index="2">
		    <template slot="title">
		      <i class="el-icon-star-on"></i>
		      <span>活动管理</span>
		    </template>
		    <el-menu-item index="active">
		      <i class="el-icon-menu"></i>
		      <span>活动策划列表</span>
		    </el-menu-item>
		  </el-submenu>
          <el-submenu index="3">
            <template slot="title">
              <i class="el-icon-star-on"></i>
              <span>用户管理</span>
            </template>
            <el-menu-item index="users">
              <i class="el-icon-menu"></i>
              <span>用户列表</span>
            </el-menu-item>
          </el-submenu>
          <!--2-->
          <el-submenu index="4">
            <template slot="title">
              <i class="el-icon-star-on"></i>
              <span>权限管理</span>
            </template>
            <el-menu-item index="role">
              <i class="el-icon-menu"></i>
              <span>角色列表</span>
            </el-menu-item>
            <el-menu-item index="rights">
              <i class="el-icon-menu"></i>
              <span>权限列表</span>
            </el-menu-item>
          </el-submenu>
          <!--3-->
          <el-submenu index="5">
            <template slot="title">
              <i class="el-icon-star-on"></i>
              <span>商品管理</span>
            </template>
            <el-menu-item index="5-1">
              <i class="el-icon-menu"></i>
              <span>商品列表</span>
            </el-menu-item>
            <el-menu-item index="5-2">
              <i class="el-icon-menu"></i>
              <span>分类参数</span>
            </el-menu-item>
            <el-menu-item index="5-3">
              <i class="el-icon-menu"></i>
              <span>商品分类</span>
            </el-menu-item>
          </el-submenu>
          <!--4-->
          <el-submenu index="6">
            <template slot="title">
              <i class="el-icon-star-on"></i>
              <span>订单管理</span>
            </template>
            <el-menu-item index="6-1">
              <i class="el-icon-menu"></i>
              <span>订单列表</span>
            </el-menu-item>
          </el-submenu>
          <!--5-->
          <el-submenu index="7">
            <template slot="title">
              <i class="el-icon-star-on"></i>
              <span>数据统计</span>
            </template>
            <el-menu-item index="reports">
              <i class="el-icon-menu"></i>
              <span>数据报表</span>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-main class="main">
        <router-view />
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  //如果没有登陆过 就不能进入到home组件
  //newVue之前自动触发
  // beforeCreate() {
  //   //获取token
  //   const token = localStorage.getItem("token");
  //   if (!token) {
  //     //token 没有 ->登录
  //     this.$router.push({ name: "login" });
  //   }
  //   //if token 有 -> 继续渲染组件
  // },
  methods: {
    handleSignout() {
      //清除token
      localStorage.clear();
      //提示
      this.$message.success("退出成功");
      //来到login组件
      this.$router.push({ name: "login" });
    }
  }
};
</script>

<style scoped>
.container {
  height: 100%;
}
.header {
  background-color: #b3c0d1;
}
.aside {
  background-color: #d3dce6;
}
.main {
  background-color: #e9eef3;
  height: 100%;
}
/*头部样式*/
.middle {
  text-align: center;
  color: azure;
}
.loginout {
  line-height: 60px;
  text-decoration: none;
}
</style>


