<template>
  <div class="login-warp">
    <el-form class="login-from" label-position="top" label-width="80px" :model="formdata">
      <h2>用户登录</h2>
      <el-form-item label="用户名">
        <el-input  v-model="formdata.username"></el-input>
      </el-form-item>
      <el-form-item label="密码">
        <el-input type="password"  v-model="formdata.password" ></el-input>
      </el-form-item>
      <el-button class="login-btn" @click.prevent="handleLogin()" type="primary">登录</el-button>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      formdata: {
        username: "",
        password: ""
      }
    };
  },
  methods: {
    async handleLogin() {
      //    this.$http.post('login',this.formdata).then((res)=>{
      
      const res = await this.$http.post("login", this.formdata);
      //对象解构赋值写法之一他指的是把等号右侧中的data对象赋值给const里面的data
      //第二行，去等号右侧res.data中找到meta的值，里面有一个msg，一个状态码
      const {
        data, //meta ,这是对象解构赋值第一种写法
        meta: { msg, status }
      } = res.data;
     
      if (status === 200) {
        //登陆成功
        //保存token
        
        localStorage.setItem("token", data.token);
         
        //跳转home
        this.$router.push({ name: "home" });
        //提示成功
        this.$message.success(msg);
      } else {
        this.$message.warning(msg);
      }
    }
  }
};
</script>

<style scoped>
.login-warp {
  height: 100%;
  background-color: rgb(70, 83, 83);
  display: flex;
  justify-content: center;
  align-items: center;
}
.login-warp .login-from {
  width: 30%;
  min-width: 300px;
  background-color: aliceblue;
  border-radius: 5px;
  padding: 30px;
}
.login-warp .login-btn {
  width: 100%;
}
</style>


