import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/login/login.vue'
import Home from '@/components/home/home.vue'
import Users from '@/components/users/users.vue'
import Right from '@/components/rights/right.vue'
import Role from '@/components/rights/role.vue'
import Reports from '@/components/reports/reports.vue'
import Tags from '@/components/tag/taglist.vue'
import Active from '@/components/active/activeList.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name:'login',
      component:Home

    },
    {
      path: '/login',
      name:'login',
      component:Home

    },
    {
      path: '/home',
      name:'home',
      component:Home,
      children:[
          {
              name:'tag',
              path:'/tag',
              component:Tags
          },
		  {
		      name:'active',
		      path:'/active',
		      component:Active
		  },
        {
          name:'users',
          path:'/users',
          component:Users
        },
        {
          name:'right',
          path:'/rights',
          component:Right
        },
        {
          name:'role',
          path:'/role',
          component:Role
        },
        {
          name:'reports',
          path:'/reports',
          component:Reports
        },
      ]

    }
  ]
})



