<template>
	<div style="width: 900px!important;height: 1000px!important;padding-bottom100px;margin-bottom: 100px;" id="UEtor">
		<script id="editor" type="text/plain"></script>
	</div>
</template>

<script>
	export default {
		name: 'UEtor',
		data() {
			return {
				editor: null,

			}
		},
		props: {
			defaultMsg: {
				type: String
			},
			config: {
				type: Object
			},
			aaa: {
				type: String
			},
		},

		mounted() {

			this.value = 'aaad'
			this.initEditor();

		},
		methods: {
			initEditor() {
				const _this = this;
				//dom元素已经挂载上去了
				this.$nextTick(() => {
					this.instance = UE.getEditor("editor", this.config);
					// 绑定事件，当 UEditor 初始化完成后，将编辑器实例通过自定义的 ready 事件交出去
					this.instance.addListener('ready', () => {
						this.ready = true;
						this.$emit('ready', this.instance);

						this.instance.setContent(this.aaa);
					});
				});
			},
			getUEcontent() {
				var ue = UE.getEditor("editor");
				return ue.getContent();


			},

		}

	}
</script>

<style>
</style>



