<template>
	<el-card class="box-card">
		<!--面包屑-->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item>首页</el-breadcrumb-item>
			<el-breadcrumb-item>标签管理</el-breadcrumb-item>
			<el-breadcrumb-item>标签列表</el-breadcrumb-item>
		</el-breadcrumb>
		<!--搜索框-->
		<el-row class="searchRow">
			<el-col :span="6">
				<el-button type="success" @click="showAddTagDialog()">添加标签</el-button>
			</el-col>
		</el-row>
		<!--表格-->
		<el-table height="350px" :data="tableData.tagsList" style="width: 100%">
			<el-table-column label="#" width="60" type="index"></el-table-column>
			<el-table-column prop="tabName" label="标签名称" width="80"></el-table-column>
			<el-table-column prop="createTime" label="创建时间"></el-table-column>
			<el-table-column prop="createUser" label="创建人"></el-table-column>
			<el-table-column prop="modifyTime" label="修改时间"></el-table-column>
			<el-table-column prop="modifyUser" label="修改人"></el-table-column>
			<el-table-column prop="address" label="操作">
				<template slot-scope="scope">
					<el-button type="primary" size="mini" plain icon="el-icon-edit" @click="showEditTagDia(scope.row)" circle>
					</el-button>
					<!--删除按钮-->
					<el-button type="danger" size="mini" plain icon="el-icon-delete" @click="showDeleTagMsgBox(scope.row.id)" circle></el-button>

				</template>
			</el-table-column>
		</el-table>
		<!--分页-->
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="tableData.pages.page"
		 :page-sizes="[5,10,15,20]" :page-size="tableData.pages.size" layout=" total,sizes, prev, pager, next, jumper" :total="tableData.pages.totalElements"></el-pagination>

		<!--对话框-->
		<!--添加用户的对话框-->
		<el-dialog title="添加用户" :visible.sync="dialogFormVisibleAdd">
			<el-form :model="form">
				<el-form-item label="标签名称" label-width="100px">
					<el-input v-model="form.tabName" autocomplete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogFormVisibleAdd = false">取 消</el-button>
				<el-button type="primary" @click="addTags()">确 定</el-button>
			</div>
		</el-dialog>

		<!--编辑用户的对话框-->
		<el-dialog title="编辑标签" :visible.sync="dialogFormVisibleEdit">
			<el-form :model="form">
				<el-form-item label="用户名" label-width="100px">
					<el-input v-model="form.tabName" autocomplete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogFormVisibleEdit = false">取 消</el-button>
				<el-button type="primary" @click="updateTag(form.id)">确 定</el-button>
			</div>
		</el-dialog>

	</el-card>
</template>
<script>
	var _self;
	export default {
		data() {
			return {
				query: "",
				userlist: [],
				total: -1,
				pagenum: 1,
				pagesize: 2,
				dialogFormVisibleAdd: false,
				dialogFormVisibleEdit: false,
				dialogFormVisibleRol: false,
				form: {
					id: null,
					tabName: "",
				},
				//分配角色
				currRoleId: 0,
				// currUserId:-1
				currUsername: '',
				roles: [],
				tableData: {
					pages: {
						// page:1,
						// size:10,
						// totalElements:1,
					},
					tagsList: [
						// {
						//     id:1,
						//     tabName:"22",
						//     createTime:"33",
						//     createUser:"44",
						//     modifyTime:"55",
						//     modifyUser:"66"
						// }
					]
				},

			}
		},
		created() {
			//获得标签列表
			// _seft = this;
			this.queryTagList();
		},
		methods: {

			//获取标签列表
			queryTagList() {
				let that = this;
				this.$axios.post('http://47.92.92.196:8080/activity/api/tag/list', {
						"page": 1,
						"pageSize": 5
					}).then(function(response) {
						// console.log(response.data.content)
						console.log(response.data.content)
						that.tableData.tagsList = response.data.content;
						that.tableData.page = response.data.page;
						that.tableData.size = response.data.size;
						that.tableData.totalElements = response.data.totalElements;
						console.log(response);
					})
					.catch(function(error) {
						console.log(error);
					});



			},
			handleCurrentChange(cpage) {
				console.log(1)
				let that = this;
				console.log(cpage)

				this.$axios.post('http://47.92.92.196:8080/activity/api/tag/list', {
						"page": cpage,
						"pageSize": that.tableData.size
					}).then(function(response) {
						// console.log(response.data.content)
						console.log(response.data.content)
						that.tableData.tagsList = response.data.content;
						that.tableData.page = response.data.page;
						that.tableData.size = response.data.size;
						that.tableData.totalElements = response.data.totalElements;
						console.log(response);
					})
					.catch(function(error) {
						console.log(error);
					});

			},
			handleSizeChange(cpage) {
				console.log(1)
				let that = this;
				console.log(cpage)

				this.$axios.post('http://47.92.92.196:8080/activity/api/tag/list', {
						"page": that.tableData.page,
						"pageSize": cpage
					}).then(function(response) {
						// console.log(response.data.content)
						console.log(response.data.content)
						that.tableData.tagsList = response.data.content;
						that.tableData.page = response.data.page;
						that.tableData.size = response.data.size;
						that.tableData.totalElements = response.data.totalElements;
						console.log(response);
					})
					.catch(function(error) {
						console.log(error);
					});

			},
			//显示添加标签弹框
			showAddTagDialog() {
				this.form = {};
				this.dialogFormVisibleAdd = true;
			},

			//添加标签
			async addTags() {
				var tagName = this.form.tabName;
				var that = this;
				this.$axios.post('http://47.92.92.196:8080/activity/api/tag/add', {

						"tabName": tagName
					}).then(function(response) {
						that.tableData.pages.page = 1;
						that.queryTagList();
						that.dialogFormVisibleAdd = false;
					})
					.catch(function(error) {
						console.log(error);
					});


			},

			//编辑按钮，打开对话框
			showEditTagDia(tag) {
				this.form = tag;
				this.dialogFormVisibleEdit = true;
			},

			//修改Tag
			updateTag(id) {
				console.log(id);
				console.log(this.form.tabName)
				var tagName = this.form.tabName;
				var that = this;
				this.$axios.put('http://47.92.92.196:8080/activity/api/tag/modify', {
						"id": id,
						"tabName": tagName
					}).then(function(response) {
						that.queryTagList();
						that.dialogFormVisibleEdit = false;
					})
					.catch(function(error) {
						console.log(error);
					});
				//                 const result = await this.$http.post("/api/tag/update",this.form);
				// 
				//                 if(result.code != 200){
				//                     this.$message.warning("更新标签失败！");
				//                     return;
				//                 }

			},

			//删除用户 - 打开消息盒子 （config）
			showDeleTagMsgBox(userId) {
				var that = this;
				this.$confirm('是否删除标签?', '提示', {
						confirmButtonText: '确定',
						cancelButtonText: '取消',
						type: 'warning'
					})
					.then(() => {
						//发送请求

						this.$axios.get('http://47.92.92.196:8080/activity/api/tag/delete/' + userId)
							.then(function(response) {
								//提示
								that.$message({
									type: 'success',
									message: '删除成功'
								});
								that.tableData.pages.page = 1;
								//更新视图
								that.queryTagList();

							})
							.catch(function(error) {
								console.log(error);
							});

					}).catch(() => {
						this.$message({
							type: 'info',
							message: '已取消删除'
						});
					});
			},
		}
	};
</script>
<style scoped>
	.box-card {
		height: 100%;
	}

	.inputSearch {
		min-width: 30%;
	}

	.searchRow {
		margin-top: 20px;
	}
</style>



