<style scoped="scoped">
	li{
		    background: #e3e8ea;
		    color: #000;
			list-style: none;
			display: inline-block;
			padding: 0 5px;
			border-radius: 6px;
			margin-right: 6px;
	}
	li.active{
	               list-style: none;
	               display: inline-block;
	               padding: 0 5px;
	               background: #40A8CD;
	               color: #fff;
	               border-radius: 6px;
				   margin-right: 6px;
	       }
		   .addBtn{
			       display: inline-block;
			       line-height: 1;
			       white-space: nowrap;
			       cursor: pointer;
			       background: #FFF;
			       border: 1px solid #DCDFE6;
			       color: #606266;
			       -webkit-appearance: none;
			       text-align: center;
			       -webkit-box-sizing: border-box;
			       box-sizing: border-box;
			       outline: 0;
			       margin: 0;
			       -webkit-transition: .1s;
			       -o-transition: .1s;
			       transition: .1s;
			       font-weight: 500;
			       padding: 12px 20px;
			       font-size: 14px;
			       border-radius: 4px;
			      
				       background: #85ce61;
				       border-color: #85ce61;
				       color: #FFF;
				       margin-top: 10px;
				       margin-bottom: 10px;
		   }
</style>
<template>
	<el-card class="box-card">
		<!--面包屑-->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item>首页</el-breadcrumb-item>
			<el-breadcrumb-item>活动管理</el-breadcrumb-item>
			<el-breadcrumb-item>活动列表</el-breadcrumb-item>
		</el-breadcrumb>
		<!--搜索框-->
		<el-row class="searchRow">
			<!-- - -->
			<el-col :span="6">
				<el-button type="success" @click="showAddTagDialog()">添加活动</el-button>
			</el-col>
		</el-row>

		<!--表格-->
		<el-table height="350px" :data="tableData.activeList" style="width: 100%">
			<el-table-column label="#" width="60" type="index"></el-table-column>
			<el-table-column prop="title" label="标题" width="80"></el-table-column>
			<el-table-column prop="desccription" label="简介"></el-table-column>

			<el-table-column prop="picUrl" label="活动图片">
				<template scope="scope">
					<img :src="scope.row.picUrl" width="200" height="200" class="head_pic" />
				</template>
				<!-- <img :src="picUrl" alt="">	 -->
			</el-table-column>
			<!-- <el-table-column prop="createUser" label="是否删除"></el-table-column> -->
			<el-table-column prop="createTime" label="创建时间"></el-table-column>
			<el-table-column prop="createUser" label="创建人"></el-table-column>
			<el-table-column prop="modifyTime" label="修改时间"></el-table-column>
			<el-table-column prop="modifyUser" label="修改人"></el-table-column>
			<el-table-column prop="address" label="操作">
				<template slot-scope="scope">
					<el-button type="primary" size="mini" plain icon="el-icon-edit" @click="showEditTagDia(scope.row)" circle>
					</el-button>
					<!--删除按钮-->
					<el-button type="danger" size="mini" plain icon="el-icon-delete" @click="showDeleTagMsgBox(scope.row.id)" circle></el-button>

				</template>
			</el-table-column>
		</el-table>
		<!--分页-->
		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="tableData.pages.page"
		 :page-sizes="[5,10,15,20]" :page-size="tableData.pages.size" layout=" total,sizes, prev, pager, next, jumper" :total="tableData.pages.totalElements">
		</el-pagination>






		<!--对话框-->
		<!--添加用户的对话框-->
		<el-dialog title="添加活动" :visible.sync="dialogFormVisibleAdd">
			<el-form :model="addForm">
				<el-form-item label="标题" label-width="100px">
					<el-input v-model="addForm.title" autocomplete="off"></el-input>
				</el-form-item>
			</el-form>
			<el-form :model="addForm">
				<el-form-item label="简介" label-width="100px">
					<textarea name="" id="" cols="30" v-model="addForm.desccription" rows="10"></textarea>
					<!-- <el-tex v-model="form.desccription" autocomplete="off"></el-input> -->
				</el-form-item>
			</el-form>
			<el-form :model="addForm">
				<el-form-item label="开始日期" label-width="100px">
					<el-input v-model="addForm.beginDate" autocomplete="off"></el-input>
				</el-form-item>
			</el-form>

			<el-form>
				<el-form-item label="全部标签" label-width="100px">
					<li v-for="(item, index) in abd" class="adwe" :class="item.check?'active':''" :data-index="index" @click="setListCheck2(index,item.id)">
						<span>{{ item.tabName }}</span>
					</li>


				</el-form-item>
			</el-form>


			<!-- <el-form-item label="活动图片" label-width="100px"> -->
			<div>
				<form :model="addForm" class="formData" id="formDataAdd" enctype="multipart/form-data">
					<label>
						<input type="file" id="fileAdd" name="file" @change="changepicAdd(this)">
						<img :src="addForm.picUrl" alt="">
					</label>
				</form>
				<button class="addBtn" @click="uploadImgAdd">上传活动图片</button>
			</div>
			

			<!-- </el-form-item> -->

			<UEtoradd :config=config ref="addUe"></UEtoradd>
			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogFormVisibleAdd = false">取 消</el-button>
				<el-button type="primary" @click="addTags()">确 定</el-button>
			</div>
		</el-dialog>


		<!--编辑用户的对话框-->
		<el-dialog title="编辑标签" :visible.sync="dialogFormVisibleEdit">
			<el-form :model="form">
				<el-form-item label="标题" label-width="100px">
					<el-input v-model="form.title" autocomplete="off"></el-input>
				</el-form-item>
			</el-form>
			<el-form :model="form">
				<el-form-item label="简介" label-width="100px">
					<textarea name="" id="" cols="30" v-model="form.desccription" rows="10"></textarea>
					<!-- <el-tex v-model="form.desccription" autocomplete="off"></el-input> -->
				</el-form-item>
			</el-form>
			<el-form :model="form">
				<el-form-item label="开始日期" label-width="100px">
					<el-input v-model="form.beginDate" autocomplete="off"></el-input>
				</el-form-item>
			</el-form>
			<el-form :model="form">
				<el-form-item label="标签" label-width="100px">
					<li v-for="(item, index) in form.tabs" class="dsaf" :class="item.check?'active':''" :data-index="index" @click="setListCheck(index,item.id)">
						<span>{{ item.tabName }}</span>
					</li>
					<!-- <div v-for="(item,index) in form.tabs">
						<input type="checkbox" name="check" class="selBtn" :data-index="data-index" checked="checked" :data-id="item.id" @click="setListCheck(index,item.id)">{{item.tabName}}
					</div> -->

				</el-form-item>
			</el-form>
			<el-form>
				<el-form-item label="全部标签" label-width="100px">
					<li v-for="(item, index) in abd" class="adwe" :class="item.check?'active':''" :data-index="index" @click="setListCheck2(index,item.id)">
						<span>{{ item.tabName }}</span>
					</li>


				</el-form-item>
			</el-form>

			<!-- 	<el-form :model="form">
				<el-form-item label="活动图片" label-width="100px">
					<input type="file" id="file" name="file" @change="changepic(this)">

					<img :src="form.picUrl" alt="">

				</el-form-item>
			</el-form> -->
			<div>
				<form :model="form" class="formData" id="formData" enctype="multipart/form-data">
				<label>
					<input type="file" id="file" name="file" @change="changepic(this)">
					<img :src="form.picUrl" alt="">
				</label>
			</form>
			<button class="addBtn" @click="uploadImg">上传活动图片</button>
			</div>
			

			<UEtor :aaa=aaa :config=config ref="ue"></UEtor>

			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogFormVisibleEdit = false">取 消</el-button>
				<el-button type="primary" @click="updateTag()">确 定</el-button>
			</div>
		</el-dialog>

	</el-card>
</template>


<script>
	import Vue from 'vue'
	import UEtor from '@/components/UE/UE.vue'
	Vue.component("UEtor", UEtor)

	export default {

		data() {
			return {
				config: {
					initialFrameWidth: 900,
					initialFrameHeight: 1000,
					zIndex: 3000
				},
				// 后台传的富文本内容
				aaa: '',
				doUpload: '/api/up/file',
				pppss: {
					srid: ''
				},

				query: "",
				userlist: [],
				total: -1,
				pagenum: 1,
				pagesize: 2,
				dialogFormVisibleAdd: false,
				dialogFormVisibleEdit: false,
				dialogFormVisibleRol: false,
				form: {
					id: null,
					tabName: "",
				},
				addForm: {
					picUrl: ''
				},
				vals: '',
				//分配角色
				currRoleId: 0,
				// currUserId:-1
				currUsername: '',
				roles: [],
				picObj: '',
				// new 选中标签id数组
				arr: [],
				//添加模块 new 选中标签id数组
				arrAdd:[],
				// 全部标签
				abd: [
					// {
					// 	id: 4,
					// 	tabName: '苹果'
					// },
					// {
					// 	id: 2,
					// 	tabName: '香蕉'
					// },
					// {
					// 	id: 3,
					// 	tabName: '橘子'
					// }
				],
				tableData: {
					pages: {
						// page: 1,
						// size: 10,
						// totalElements: 1,
					},
					activeList: [

					]
				},

			}
		},


		created() {

		},

		methods: {

			// 选中标签
			setListCheck(idx) {

				console.log(idx)
				var check = this.form.tabs[idx].check;
				console.log(check)
				this.form.tabs[idx].check = !check;

			},
			// 全部标签
			setListCheck2(idx) {
				console.log(idx)
				var check = this.abd[idx].check;
				console.log(check)
				this.abd[idx].check = !check;
			},

			changepicAdd(aa) {
				var _this = this;
				var reads = new FileReader();
				var f = document.getElementById('fileAdd').files[0];
				console.log(f)
				_this.picObj = f;
				var aa = reads.readAsDataURL(f);
				console.log(aa);
				reads.onload = function(e) {
					console.log(e.target.result)
					_this.addForm.picUrl = this.result;
					// console.log(_this.form.picObj);
					var a = e.target.result
					console.log(a)
					_this.addForm.picUrl = a;
				};

			},
			// 编辑上传图片
			uploadImg() {
				var _this = this;
				var form = document.getElementById('formData');
				var fd = new FormData(form);
				console.log(fd)
				this.$axios.post('http://47.92.92.196:8080/activity/api/activityPage/upload', fd).then(function(response) {
						_this.form.picUrl = response.data.data;
						_this.$message({
							type: 'success',
							message: '上传成功'
						});
					})
					.catch(function(error) {
						console.log(error);
					});

			},
			// 添加上传图片
			uploadImgAdd() {
				var _this = this;
				var form = document.getElementById('formDataAdd');
				var fd = new FormData(form);
				console.log(fd)
				this.$axios.post('http://47.92.92.196:8080/activity/api/activityPage/upload', fd).then(function(response) {
						_this.addForm.picUrl = response.data.data;
						_this.$message({
							type: 'success',
							message: '上传成功'
						});
					})
					.catch(function(error) {
						console.log(error);
					});

			},
			// 选择图片
			changepic(aa) {
				var _this = this;
				var reads = new FileReader();
				var f = document.getElementById('file').files[0];
				console.log(f)
				_this.picObj = f;
				var aa = reads.readAsDataURL(f);
				console.log(aa);
				reads.onload = function(e) {
					console.log(e.target.result)
					_this.form.picUrl = this.result;
					// console.log(_this.form.picObj);
					var a = e.target.result
					console.log(a)
					// _this.form.picUrl = a;
				};

			},
			//获取活动列表
			queryTagListaa: function() {
				let that = this;
				this.$axios.post('http://47.92.92.196:8080/activity/api/activity/list', {
						"page": 1,
						"pageSize": 5
					}).then(function(response) {


						that.tableData.activeList = response.data.data.content;
						console.log(that.tableData.activeList[0].title)
						that.tableData.page = response.data.data.page;
						that.tableData.size = response.data.data.size;
						that.tableData.totalElements = response.data.data.totalElements;

					})
					.catch(function(error) {
						console.log(error);
					});


			},
			//获取标签列表
			queryTagList() {
				let that = this;
				this.$axios.post('http://47.92.92.196:8080/activity/api/tag/list', {
						"page": 1,
						"pageSize": 1000
					}).then(function(response) {
						// console.log(response.data.content)
						console.log(response.data.content)

						that.abd = response.data.content;
						console.log(that.abd)
						that.abd.forEach((item, index) => {
							Vue.set(item, 'check', false)
						});
					})
					.catch(function(error) {
						console.log(error);
					});

			},
			handleCurrentChange(cpage) {
				console.log(1)
				let that = this;
				console.log(cpage)

				this.$axios.post('http://47.92.92.196:8080/activity/api/activity/list', {
						"page": cpage,
						"pageSize": that.tableData.size
					}).then(function(response) {
						// console.log(response.data.content)
						console.log(response.data.content)
						that.tableData.activeList = response.data.data.content;
						that.tableData.page = response.data.data.page;
						that.tableData.size = response.data.data.size;
						that.tableData.totalElements = response.data.data.totalElements;
						console.log(response);
					})
					.catch(function(error) {
						console.log(error);
					});

			},
			handleSizeChange(cpage) {
				console.log(1)
				let that = this;
				console.log(cpage)

				this.$axios.post('http://47.92.92.196:8080/activity/api/activity/list', {
						"page": that.tableData.page,
						"pageSize": cpage
					}).then(function(response) {
						// console.log(response.data.content)
						console.log(response.data.content)
						that.tableData.activeList = response.data.data.content;
						that.tableData.page = response.data.data.page;
						that.tableData.size = response.data.data.size;
						that.tableData.totalElements = response.data.data.totalElements;
						console.log(response);
					})
					.catch(function(error) {
						console.log(error);
					});

			},
			//显示添加活动弹框
			showAddTagDialog() {
				this.form = {};
				this.queryTagList()
				this.dialogFormVisibleAdd = true;
			},

			//添加标签
			addTags() {
				var that = this;

				var UEcontentadd = this.$refs.addUe.getUEcontentadd();
				console.log(UEcontentadd)

				let arrAdd = [] // 选中活动标签数组
				for (var i = 0; i < this.abd.length; i++) {
					if (this.abd[i].check) {
						arrAdd.push(this.abd[i].id);
						console.log(this.abd[i].id)
					}
				}
				let arrayAdd = Array.from(new Set(arrAdd));

				this.arrAdd = arrayAdd;

				console.log(this.arrAdd)
				// console.log(this.form);

				this.$axios.post('http://47.92.92.196:8080/activity/api/activity/add', {
						"id": that.addForm.id,
						"title": that.addForm.title,
						"desccription": that.addForm.desccription,
						"content": UEcontentadd,
						"picUrl": that.addForm.picUrl,
						"beginDate": that.addForm.beginDate,
						"tagIds": that.arrAdd,
					}).then(function(response) {
						that.$message({
							type: 'success',
							message: '修改成功'
						});
						that.dialogFormVisibleEdit = false;
					})
					.catch(function(error) {
						console.log(error);
					});
				this.tableData.pages.page = 1;
				this.queryTagList();

				this.dialogFormVisibleAdd = true;

			},

			//编辑按钮，打开对话框
			showEditTagDia(tag) {

				let that = this;
				var tagId = tag.id;
				that.$axios.get('http://47.92.92.196:8080/activity/api/activity/detail/' + tagId).then(function(response) {
					// console.log(response.data.data)
					that.form = response.data.data;
					that.aaa = that.form.content;
					that.form.tabs.forEach((item, index) => {
						Vue.set(item, 'check', true)
					})
				})
				// .catch(function(error) {
				// 	console.log(error);
				// });
				that.queryTagList();
				this.dialogFormVisibleEdit = true;



			},

			//修改活动
			updateTag() {

				var _that = this;
				var UEcontent = this.$refs.ue.getUEcontent();
				console.log(UEcontent)

				let arr = [] // 选中活动标签数组
				for (var i = 0; i < this.form.tabs.length; i++) {
					if (this.form.tabs[i].check) {
						arr.push(this.form.tabs[i].id);
						console.log(this.form.tabs[i].id)
					}
				}
				for (var i = 0; i < this.abd.length; i++) {
					if (this.abd[i].check) {
						arr.push(this.abd[i].id);
						console.log(this.abd[i].id)
					}
				}
				let array = Array.from(new Set(arr));

				this.arr = array;

				console.log(this.arr)
				// console.log(this.form);
				let that = this
				console.log(that.picObj);
				this.$axios.post('http://47.92.92.196:8080/activity/api/activity/modify', {
						"id": that.form.id,
						"title": that.form.title,
						"desccription": that.form.desccription,
						"content": UEcontent,
						"picUrl": that.form.picUrl,
						"beginDate": that.form.beginDate,
						"tagIds": that.arr,
					}).then(function(response) {
						that.$message({
							type: 'success',
							message: '修改成功'
						});
						that.dialogFormVisibleEdit = false;
					})
					.catch(function(error) {
						console.log(error);
					});
			},

			//删除用户 - 打开消息盒子 （config）
			showDeleTagMsgBox(userId) {
				var that = this;
				this.$confirm('是否删除标签?', '提示', {
						confirmButtonText: '确定',
						cancelButtonText: '取消',
						type: 'warning'
					})
					.then(async () => {
						//发送请求
						this.$axios.get('http://47.92.92.196:8080/activity/api/activity/delete/' + userId)
							.then(function(response) {
								//提示
								that.$message({
									type: 'success',
									message: '删除成功'
								});
								that.tableData.pages.page = 1;
								//更新视图
								that.queryTagList();

							})
							.catch(function(error) {
								console.log(error);
							});

					}).catch(() => {
						this.$message({
							type: 'info',
							message: '已取消删除'
						});
					});
			},
		},
		mounted() {
			this.queryTagListaa();

		}
	};
</script>
<style scoped>
	.box-card {
		height: 100%;
	}

	.inputSearch {
		min-width: 30%;
	}

	.searchRow {
		margin-top: 20px;
	}
</style>



