<template>
  <el-card class="box-card">
    <!--面包屑-->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!--搜索框-->
    <el-row class="searchRow">
       <el-col :span="10">
         <el-input
          @clear="loadUserList()"
          clearable
          placeholder="请输入内容"
          v-model="query"
          clear
          class="inputSearch"
         >
          <el-button @click="searchUser()" slot="append" icon="el-icon-search"></el-button>
        </el-input>
      </el-col>
     <el-col :span="6">
        <el-button type="success" @click="showAddUserDia()">添加用户</el-button>
      </el-col>
    </el-row>
    <!--表格-->
    <el-table
      height="350px"
      :data="userlist"
      style="width: 100%">
      <el-table-column label="#" width="60" type="index"></el-table-column>
      <el-table-column prop="username" label="姓名" width="80"></el-table-column>
      <el-table-column prop="email" label="邮箱"></el-table-column>
      <el-table-column prop="mobile" label="电话"></el-table-column>
      <el-table-column prop="cread_time" label="创建时间">
        <!--全局过滤器，处理日期格式-->
        <template slot-scope="scop">{{scop.row.create_time | fmtdate}}</template>
      </el-table-column>
      <el-table-column label="用户状态">
        <template slot-scope="scope">
          <el-switch 
          @change="changeMgState(scope.row)"
          v-model="scope.row.mg_state" 
          active-color="#13ce66"
           inactive-color="#ff4949"></el-switch>
        </template>
      </el-table-column>
      <el-table-column prop="address" label="操作">
        <template  slot-scope="scope">
          <el-button type="primary"
           size="mini"
           plain icon="el-icon-edit" 
           @click="showEditUserDia(scope.row)"
           circle>
          </el-button>
          <el-button 
          @click="showEditUserRoleDia(scope.row)"
          type="success"
          size="mini"
          plain icon="el-icon-check"
          circle>
          </el-button>
          <!--删除按钮-->
          <el-button
            type="danger"
            size="mini"
            plain
            icon="el-icon-delete"
            @click="showDeleUserMsgBox(scope.row.id)"
            circle
          ></el-button>
          
        </template>
      </el-table-column>
    </el-table>
    <!--分页-->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pagenum"
      :page-sizes="[2, 4, 6, 8]"
      :page-size="2"
      layout=" total,sizes, prev, pager, next, jumper"
      :total="total"
    ></el-pagination>

    <!--对话框-->
    <!--添加用户的对话框-->
    <el-dialog title="添加用户" :visible.sync="dialogFormVisibleAdd">
      <el-form :model="form">
        <el-form-item label="用户名" label-width="100px">
          <el-input disabled v-model="form.username" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="密码" label-width="100px">
          <el-input v-model="form.password" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="邮箱" label-width="100px">
          <el-input v-model="form.email" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="电话" label-width="100px">
          <el-input v-model="form.mobile" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibleAdd = false">取 消</el-button>
        <el-button type="primary" @click="addUser()">确 定</el-button>
      </div>
    </el-dialog>

    <!--编辑用户的对话框-->
     <el-dialog title="编辑用户" :visible.sync="dialogFormVisibleEdit">
      <el-form :model="form">
        <el-form-item label="用户名" label-width="100px">
          <el-input v-model="form.username" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="邮箱" label-width="100px">
          <el-input v-model="form.email" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="电话" label-width="100px">
          <el-input v-model="form.mobile" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibleEdit = false">取 消</el-button>
        <el-button type="primary" @click="dialogFormVisibleEdit=false">确 定</el-button>
      </div>
    </el-dialog>

    <!--角色弹框-->
     <el-dialog title="分配角色" :visible.sync="dialogFormVisibleRol">
  <el-form :model="form">
    <el-form-item label="用户名" label-width="100px">
       {{"currUsername"}}
    </el-form-item>
    <el-form-item label="角色" label-width="100px">
<!--如果select的绑定的数据的值和option的value一样，就会显示option的label值-->
            {{currRoleId}}
      <el-select v-model="currRoleId" >
             
        <el-option label="请选择" :value="-1"></el-option>
        <el-option :label="item.roleName" :value="item.id"
        v-for="(item,i) in roles" :key="i">
          
        </el-option>
      </el-select>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogFormVisibleRol = false">取 消</el-button>
    <el-button type="primary" @click="dialogFormVisibleRol = false">确 定</el-button>
  </div>
</el-dialog>
  </el-card>
</template>
<script>
export default {
  data() {
    return {
      query: "",
      userlist: [],
      total: -1,
      pagenum: 1,
      pagesize: 2,
      dialogFormVisibleAdd: false,
      dialogFormVisibleEdit:false,
      dialogFormVisibleRol:false,
      form: {
        username: "",
        password: "",
        email: "",
        mobile: ""
      },
      //分配角色
      currRoleId:0,
      // currUserId:-1
      currUsername:'',
      roles:[]
    }
  },
  created() {
    this.getUserList();
  },
  methods: {
    //分配角色-打开对话框
   async showEditUserRoleDia(user){
        this.currUsername= user.username;
        // 获取所有的角色
          const res1 = await this.$http.get('roles');
          this.roles=res1.data.data
        //获取当前用户的角色id
        const res = await this.$http.get('users/'+user.id);
        //获取当前用户的角色id
       this.currRoleId =res.data.data.rid;
      // this.currRoleId =-1;
        this.dialogFormVisibleRol = true
       
    },
    //修改状态
   async changeMgState(user){
      // const res = await this.$http.put("users/${user.id}/state/${user.mg_state}");

      const res = await this.$http.put("users/"+user.id+"/state/"+user.mg_state)
      console.log(res)
    },
    //编辑按钮-发送请求
    async editUser(){
      const res =await this.$http.put("users/"+this.form.id,this.form);
      console.log(res)
    },
    //编辑按钮，打开对话框
    showEditUserDia(user){
      this.form = user;
      this.dialogFormVisibleEdit = true;

    },
      //删除用户 - 打开消息盒子 （config）
    showDeleUserMsgBox(userId){
        this.$confirm('删除用户?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        .then(async() => {
            //发送请求
            const res=await this.$http.delete("users/"+userId)
            console.log(res);
            if(res.data.meta.status===200){
            this.pagenum = 1;
                //更新视图
            this.getUserList();
                //提示
            this.$message({
            type: 'success',
            message: res.data.meta.msg
          })
                
            }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      },
     
    //添加用户发送请求
    async addUser (){
      const res = await this.$http.post("users", this.form);
      const {
        meta: { status, msg },
        data
      } = res.data;
      if (status === 201) {
        //提示成功
        this.$message.success(msg);
        //关闭对话框
        this.dialogFormVisibleAdd = false;
        //更新视图
        this.getUserList();
        //清空文本框
        this.form = {};
      } else {
        this.$message.warning(msg);
      }
    },
    //添加用户
    showAddUserDia() {
      this.form={};
      this.dialogFormVisibleAdd = true;
    },
    //清空搜索框，重新加载数据
    loadUserList() {
      this.getUserList();
    },
    //搜索用户
    searchUser() {
      //
      this.getUserList();
    },
    //分页相关方法
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.pagesize = val;
      //回到第一页
      this.pagenum = 1;
      this.getUserList();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.pagenum = val;
      this.getUserList();
    },

    //获取用户列表的请求
    async getUserList() {
      //1.在created中调方法
      //methods:{getUserlist(){发送请求}}
      //接口文档中 除了登陆之外的的所有请求都需要进行授权->设置请求头
      //找axios中的关于请求头设置的代码
      //js
      const AUTH_TOKEN = localStorage.getItem("token");
      this.$http.defaults.headers.common["Authorization"] = AUTH_TOKEN;
      //   console.log(res);
      //发送请求
      const res = await this.$http.get(
        //？后的是参数
        "users?query=" +
          this.query +
          "&pagenum=" +
          this.pagenum +
          "&pagesize=" +
          this.pagesize

        // "users?query=${this.query}&pagenum=${this.pagenum}&pagesize=${this.pagesize}"
      );

      console.log(res);
      const {
        meta: { status, msg },
      } = res.data;
      if (status === 200) {
        const {
        data: { users, total }
      } = res.data;
        //给表格数据赋值
        this.userlist = users;
        //给tatal赋值
        this.total = total;
        //提示
        this.$message.success(msg);
      } else {
        //提示
        this.$message.warning(msg);
      }
    }
  }
};
</script>
<style scoped>
.box-card {
  height: 100%;
}
.inputSearch {
  min-width: 30%;
}
.searchRow {
  margin-top: 20px;
}
</style>


